body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "Lato", sans-serif;
}

.app {
  background: url("./assets/bg3.jpg") no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
}

.floatingbox {
  background: rgba(0, 0, 0, 0.42);
  padding: 20px;
  border-radius: 7px;
  width: 450px;
}

.line {
  overflow: hidden;
  white-space: nowrap;
  height: 19px;
  font-family: "Courier", monospace;
  font-weight: bold;
}
